"use client"

import { useState, useEffect, useRef, useCallback } from "react"
import { useRouter } from "next/navigation"
import Link from "next/link"
import Image from "next/image"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Card } from "@/components/ui/card"
import { Avatar, AvatarFallback } from "@/components/ui/avatar"
import { 
  ChevronLeft, Send, Sparkles, User, Loader2, 
  PiggyBank, Wallet, TrendingDown, MessageSquare
} from "lucide-react"

// WebSocket 라이브러리 (npm install sockjs-client stompjs 필요)
import SockJS from "sockjs-client"
import Stomp from "stompjs"

export default function AIChatPage() {
  const router = useRouter()
  
  // 상태 관리
  const [userName, setUserName] = useState("")
  const [messages, setMessages] = useState<any[]>([])
  const [inputValue, setInputValue] = useState("")
  const [isTyping, setIsTyping] = useState(false)
  const [isConnected, setIsConnected] = useState(false)
  
  // 채팅방 관련 상태
  const [currentRoomId, setCurrentRoomId] = useState<string | null>(null)
  const [stompClient, setStompClient] = useState<any>(null)
  
  // 무한 스크롤 및 참조
  const scrollRef = useRef<HTMLDivElement>(null)
  const [hasMore, setHasMore] = useState(true)
  const [oldestMsgInfo, setOldestMsgInfo] = useState({ id: "", date: "" })

  const API_BASE = "http://sparta-nextme.xyz:18080"

  // 1. 초기 사용자 정보 및 WebSocket 연결
  useEffect(() => {
    const name = localStorage.getItem("name") || "사용자"
    const token = localStorage.getItem("accessToken")
    const userId = localStorage.getItem("userId") // 로그인 시 저장된 UUID 필요
    
    setUserName(name)

    if (!token) {
      alert("로그인이 필요합니다.")
      router.push("/login")
      return
    }

    const socket = new SockJS(`${API_BASE}/ws/chat`)
    const client = Stomp.over(socket)
    client.debug = () => {} // 디버그 로그 숨김

    client.connect({ Authorization: `Bearer ${token}` }, (frame) => {
      setIsConnected(true)
      setStompClient(client)
      // 연결 성공 시 AI 방 조회/생성 로직 실행
      ensureAIRoom(token, userId)
    }, (err) => {
      console.error("WebSocket Error:", err)
    })

    return () => { if (client) client.disconnect() }
  }, [])

  // 2. AI 채팅방 확보 (조회 후 없으면 생성)
  const ensureAIRoom = async (token: string, userId: string | null) => {
    try {
      // AI 타입의 방 목록 조회
      const res = await fetch(`${API_BASE}/v1/chats/room?roomType=AI`, {
        headers: { Authorization: `Bearer ${token}` }
      })
      const data = await res.json()
      const aiRooms = data.rooms || data

      if (aiRooms && aiRooms.length > 0) {
        const roomId = aiRooms[0].chatRoomId || aiRooms[0].id
        selectRoom(roomId, token)
      } else {
        // 방이 없으면 새로 생성
        const createRes = await fetch(`${API_BASE}/v1/chats/room`, {
          method: "POST",
          headers: { "Content-Type": "application/json", Authorization: `Bearer ${token}` },
          body: JSON.stringify({ roomType: "AI", userId: userId })
        })
        const newData = await createRes.json()
        if (newData.chatRoomId) selectRoom(newData.chatRoomId, token)
      }
    } catch (e) { console.error("Room init error", e) }
  }

  // 3. 방 선택 및 구독
  const selectRoom = async (roomId: string, token: string) => {
    setCurrentRoomId(roomId)
    
    // 이전 메시지 로드
    fetchMessages(roomId, token)

    // 입장 처리
    fetch(`${API_BASE}/v1/chats/room/${roomId}/join`, {
      method: "POST",
      headers: { Authorization: `Bearer ${token}` }
    })
  }

  // 4. 메시지 수신 구독 (stompClient가 업데이트될 때 실행)
  useEffect(() => {
    if (stompClient && currentRoomId) {
      const sub = stompClient.subscribe(`/topic/chat.room.${currentRoomId}`, (sdkEvent: any) => {
        const newMessage = JSON.parse(sdkEvent.body)
        setMessages(prev => [...prev, newMessage])
        setIsTyping(false)
        scrollToBottom()
      })
      return () => sub.unsubscribe()
    }
  }, [stompClient, currentRoomId])

  // 5. 메시지 목록 불러오기
  const fetchMessages = async (roomId: string, token: string) => {
    try {
      const res = await fetch(`${API_BASE}/v1/chats/message/${roomId}?size=20`, {
        headers: { Authorization: `Bearer ${token}` }
      })
      const data = await res.json()
      const msgs = Array.isArray(data) ? data : (data.messages || [])
      
      if (msgs.length > 0) {
        setMessages(msgs.reverse()) // 과거->최신 순으로 정렬
        setOldestMsgInfo({
          id: msgs[0].messageId?.chatMessageId,
          date: msgs[0].createdAt
        })
      }
      if (msgs.length < 20) setHasMore(false)
      scrollToBottom()
    } catch (e) { console.error(e) }
  }

  // 6. 메시지 전송
  const sendMessage = (text: string) => {
    if (!text.trim() || !stompClient || !currentRoomId) return

    const payload = {
      content: text,
      roomType: "AI"
    }

    stompClient.send(`/app/chat.send/${currentRoomId}`, {}, JSON.stringify(payload))
    setInputValue("")
    setIsTyping(true) // AI가 곧 응답할 것이므로 타이핑 표시
  }

  const handleAISend = (e: React.FormEvent) => {
    e.preventDefault()
    sendMessage(inputValue)
  }

  const scrollToBottom = () => {
    setTimeout(() => {
      if (scrollRef.current) {
        scrollRef.current.scrollTop = scrollRef.current.scrollHeight
      }
    }, 100)
  }

  return (
    <div className="min-h-screen bg-background text-foreground flex flex-col">
      {/* Header */}
      <header className="border-b border-border bg-card/50 backdrop-blur-sm sticky top-0 z-50">
        <div className="container mx-auto px-4 h-16 flex items-center justify-between">
          <div className="flex items-center gap-2">
            <Button variant="ghost" size="icon" onClick={() => router.back()}>
              <ChevronLeft className="h-5 w-5" />
            </Button>
            <Link href="/" className="flex items-center gap-2">
              <Image src="/logo.png" alt="Next Me" width={110} height={30} className="h-7 w-auto" />
            </Link>
          </div>
          <div className="flex items-center gap-3">
            <div className="flex flex-col items-end">
              <span className="text-[10px] font-black text-primary uppercase tracking-tighter">AI Financial Advisor</span>
              <span className="text-[9px] text-muted-foreground flex items-center gap-1">
                <div className={`w-1.5 h-1.5 rounded-full ${isConnected ? 'bg-green-500' : 'bg-red-500'}`} />
                {isConnected ? 'LIVE' : 'DISCONNECTED'}
              </span>
            </div>
            <Avatar className="h-9 w-9 border-2 border-primary/10">
              <AvatarFallback className="bg-primary/5 text-xs font-bold text-primary">{userName?.charAt(0)}</AvatarFallback>
            </Avatar>
          </div>
        </div>
      </header>

      {/* Main Chat Area */}
      <main className="container mx-auto max-w-3xl flex-1 flex flex-col p-4 md:p-6 overflow-hidden">
        <div ref={scrollRef} className="flex-1 overflow-y-auto space-y-6 pr-2 mb-4 custom-scrollbar scroll-smooth">
          {messages.length === 0 && !isTyping && (
            <div className="flex flex-col items-center justify-center h-full opacity-40">
              <Bot className="h-12 w-12 mb-2 text-primary" />
              <p className="text-sm font-bold">AI와 대화를 시작해보세요</p>
            </div>
          )}

          {messages.map((msg, idx) => {
            const isMine = msg.senderId !== "AI" // 서버 응답 구조에 따라 조정 필요
            return (
              <div key={idx} className={`flex ${isMine ? 'justify-end' : 'justify-start'} animate-in fade-in slide-in-from-bottom-2 duration-300`}>
                <div className={`flex gap-3 max-w-[85%] ${isMine ? 'flex-row-reverse' : ''}`}>
                  <div className={`h-10 w-10 rounded-xl flex items-center justify-center shrink-0 border shadow-lg ${
                    isMine ? 'bg-primary border-primary shadow-primary/20' : 'bg-card border-border shadow-black/5'
                  }`}>
                    {isMine ? <User className="h-5 w-5 text-primary-foreground" /> : <Sparkles className="h-5 w-5 text-primary" />}
                  </div>
                  <div className="flex flex-col gap-1.5">
                    <span className={`text-[10px] font-black uppercase tracking-widest text-muted-foreground/60 ${isMine ? 'text-right' : 'text-left'}`}>
                      {isMine ? userName : 'NextMe AI'}
                    </span>
                    <Card className={`p-4 rounded-2xl shadow-sm border-border/50 ${
                      isMine ? 'bg-primary text-primary-foreground rounded-tr-none border-none shadow-md' : 'bg-card/80 backdrop-blur-sm text-foreground rounded-tl-none'
                    }`}>
                      <p className="text-sm leading-relaxed font-medium whitespace-pre-wrap">{msg.content}</p>
                      <span className="text-[9px] opacity-50 mt-2 block text-right">
                        {new Date(msg.createdAt).toLocaleTimeString('ko-KR', { hour: '2-digit', minute: '2-digit' })}
                      </span>
                    </Card>
                  </div>
                </div>
              </div>
            )
          })}
          
          {isTyping && (
            <div className="flex justify-start gap-3 animate-pulse">
              <div className="h-10 w-10 rounded-xl bg-card border border-border flex items-center justify-center">
                <Loader2 className="h-5 w-5 text-primary animate-spin" />
              </div>
              <Card className="p-4 bg-card/50 rounded-2xl rounded-tl-none border-dashed">
                <div className="flex gap-1">
                  <div className="w-1.5 h-1.5 bg-primary rounded-full animate-bounce" />
                  <div className="w-1.5 h-1.5 bg-primary rounded-full animate-bounce [animation-delay:0.2s]" />
                  <div className="w-1.5 h-1.5 bg-primary rounded-full animate-bounce [animation-delay:0.4s]" />
                </div>
              </Card>
            </div>
          )}
        </div>

        {/* Suggestion Buttons */}
        {messages.length < 5 && !isTyping && (
          <div className="flex flex-wrap gap-2 mb-6 justify-center animate-in fade-in zoom-in-95 duration-500">
            <Button variant="outline" size="sm" className="rounded-full bg-card border-primary/20 hover:border-primary/50 font-bold text-xs group px-4 py-5" onClick={() => sendMessage("지출 내역 분석해줘")}>
              <TrendingDown className="h-3.5 w-3.5 mr-2 text-destructive" /> 지출 분석
            </Button>
            <Button variant="outline" size="sm" className="rounded-full bg-card border-primary/20 hover:border-primary/50 font-bold text-xs group px-4 py-5" onClick={() => sendMessage("맞춤 저축 상품 추천")}>
              <PiggyBank className="h-3.5 w-3.5 mr-2 text-primary" /> 저축 추천
            </Button>
            <Button variant="outline" size="sm" className="rounded-full bg-card border-primary/20 hover:border-primary/50 font-bold text-xs group px-4 py-5" onClick={() => sendMessage("내 자산 상태 진단")}>
              <Wallet className="h-3.5 w-3.5 mr-2 text-orange-500" /> 자산 진단
            </Button>
          </div>
        )}

        {/* Input Form */}
        <div className="relative pt-2 pb-6">
          <form onSubmit={handleAISend} className="relative group">
            <div className="absolute -inset-1 bg-gradient-to-r from-primary/20 to-accent/20 rounded-2xl blur opacity-25 group-focus-within:opacity-100 transition duration-500" />
            <Input 
              value={inputValue}
              onChange={(e) => setInputValue(e.target.value)}
              placeholder={isConnected ? "AI에게 질문해보세요..." : "서버 연결 중..."} 
              disabled={!isConnected}
              className="relative w-full h-14 pl-6 pr-14 rounded-2xl border-border/50 bg-card shadow-2xl focus-visible:ring-primary text-base"
            />
            <Button type="submit" className="absolute right-2 top-2 h-10 w-10 rounded-xl shadow-lg transition-all active:scale-95" disabled={!inputValue.trim() || isTyping || !isConnected}>
              <Send className="h-5 w-5" />
            </Button>
          </form>
          <p className="text-[10px] text-center mt-3 text-muted-foreground font-medium">
            NextMe AI는 금융 데이터를 바탕으로 조언하며, 최종 결정의 책임은 본인에게 있습니다.
          </p>
        </div>
      </main>
    </div>
  )
}